import axios from 'axios';

const instance = axios.create({
    //baseURL: 'http://localhost:3001/url' //uncomment this part when using with proxy
    baseURL: 'http://3.122.7.162:5000/v60/admin/session' //comment this part when using with proxy
});

instance.defaults.headers.post['Content-Type']="application/json"; //comment this part when using with proxy
instance.defaults.headers.post['Access-Control-Allow-Origin']="*"; //comment this part when using with proxy
instance.defaults.headers.post['Accept']="application/json"; //comment this part when using with proxy


instance.interceptors.request.use(request=>{
    return request;
},error=>{
    console.log(error);
    return Promise.reject(error);
});

instance.interceptors.response.use(response=>{
    //localStorage.setItem('set-cookie',response.data); //uncomment this part when using with proxy
    localStorage.setItem('set-cookie',"JSESSIONID=02F5C8FBC7816583C2664B1A47EB9DF4; Path=/"); //comment this part when using with proxy
    console.log("[cookie]: "+ localStorage.getItem('set-cookie'));
    return response;
},error=>{
    console.log(error);
    return Promise.reject(error);
});


//instance.

export default instance;


