import React, {Component} from 'react';
import { Redirect } from 'react-router-dom';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import Spinner from '../../components/UI/Spinner/Spinner';
import axios from '../../axios-login';
import classes from './Auth.css';
import Modal from '../../components/UI/Modal/Modal';

class Auth extends Component{
    state = {
        authenticated : false,
        show: false,
        orderForm: {
            Username: {
                elementType: 'input',
                elementConfig: {
                    name: 'username',
                    type: 'text',
                    placeholder: 'Your Username'
                },
                value: '',
                validation: {
                    required: true
                },
                valid:false,
                touched:false
            },
            Password:{
                elementType: 'input',
                elementConfig: {
                    name: 'credential',
                    type: 'password',
                    placeholder: 'Password'
                },
                value:'',
                validation:{
                    required: true
                },
                valid: false,
                touched: false
            }
        },
        loading:false
    }

    inputChangedHandler = (event, inputIdent) => {
        const updatedOrderForm = {
            ...this.state.orderForm,
            [inputIdent] : {
                ...this.state.orderForm[inputIdent],
                value: event.target.value,
                valid: this.checkValidity(event.target.value, this.state.orderForm[inputIdent].validation),
                touched: true
            }
        };
        this.setState({orderForm:updatedOrderForm});
    }

    loginHandler=(event)=>{
        event.preventDefault();
        this.setState({loading: true});
        let formData={};
        for(let formElementIdent in this.state.orderForm){

            formData[this.state.orderForm[formElementIdent].elementConfig.name]=this.state.orderForm[formElementIdent].value;
        }
        axios.post('',formData,{headers:{
            "accept":"application/json,text/html"
        }})
             .then(response=>{
                 this.setState({loading:false});
                 console.log(response);
                 this.props.history.push({pathname:'/search'});
             }).catch(except=>{
                 this.setState({
                    show: true,
                    error: except,
                    loading: false
                 });
             });    
    }

    closeHandler=()=>{
        this.setState({
            show: false,
            error: null
        });
    }

    checkValidity(value,rules){
        let isValid = true;
        if (!rules) {
            return true;
        }
        
        if (rules.required) {
            isValid = value.trim() !== '' && isValid;
        }

        return isValid;
    }

    render(){
        const formElementArray = [];
        for (let key in this.state.orderForm){
            formElementArray.push({
                id:key,
                config: this.state.orderForm[key]
            });
        }
        let redirect = null;
        if (this.props.authenticated){
            localStorage.removeItem('set-cookie');
            redirect=<Redirect to="/login"/>
        }
        let form = (
            <form onSubmit={this.loginHandler}>
                {formElementArray.map(formElement=>{
                    return(
                        <Input key={formElement.id}
                               elementType={formElement.config.elementType}
                               elementConfig={formElement.config.elementConfig}
                               value={formElement.config.value}
                               invalid={!formElement.config.valid}
                               shouldValidate={formElement.config.validation}
                               touched={formElement.config.touched}
                               label={formElement.id}
                               changed={(event)=>this.inputChangedHandler(event,formElement.id)}/>
                               
                    );
                })}
                <Button btnType="Success">SUBMIT</Button>
            </form>
        );
        if (this.state.loading) {
            form=<Spinner/>;
        }
        return(
            <div className={classes.Auth}>
                {redirect}
                <Modal show={this.state.show} modalClosed={this.closeHandler}>
                    {this.state.error?this.state.error.message+ ", Please insert correct credentials!!":null}
                </Modal>
                    {form}
            </div>
        );
    }
}

export default Auth;


